import {useState,useEffect} from 'react';
import { List ,Row ,Col , Modal ,message ,Button} from 'antd';
import axios from 'axios';
import servicePath from  '../config/apiUrl';
import '../static/css/ArticleList.css';

function ArticleList(){
    const [list,setList] = useState([]);

    function getArticleList(){
        axios(servicePath.getArticleList,{
            headers:{
            'sessionId':localStorage.getItem('openId')
            }
        }).then((res)=>{
            if(res.data.data==''){
                localStorage.removeItem('openId');
                window.location.href = '/';
            }else{
                setList(res.data.data);
            }
        });
    }

    useEffect(()=>{
        getArticleList();
    },[]);

    function handleDelete(id){
        Modal.confirm({
            title:'?',
            content:'OK',
            onOk(){
                axios(servicePath.delArticle+id,{
                    headers:{
                        'sessionId':localStorage.getItem('openId')
                    }
                }).then((res)=>{
                    message.success('')
                    getArticleList();
                });
            }
        });
       
    }

    function handleUpdate(id){
        window.location.href = '/index/addArticle/'+id;
    }

    return (
        <div>
            <List
                header={
                    <Row className="list-div">
                        <Col span={8}>
                            <b></b>
                        </Col>
                        <Col span={3}>
                            <b></b>
                        </Col>
                        <Col span={3}>
                            <b></b>
                        </Col>
                        <Col span={3}>
                            <b></b>
                        </Col>
                        <Col span={3}>
                            <b></b>
                        </Col>
                        <Col span={4}>
                            <b></b>
                        </Col>
                    </Row>
                }
                bordered
                dataSource={list}
                renderItem={(item)=>{
                    return (
                        <List.Item>
                            <Row className="list-div">
                                <Col span={8}>{item.title}</Col>
                                <Col span={3}>{item.typeName}</Col>
                                <Col span={3}>{item.addTime}</Col>
                                <Col span={3}>{item.introduce}</Col>
                                <Col span={3}>{item.view_count}</Col>
                                <Col span={4}>
                                    <Button type="primary" onClick={()=>{handleUpdate(item.id)}}></Button>&nbsp;
                                    <Button onClick={()=>handleDelete(item.id)}></Button>
                                </Col>
                            </Row>
                        </List.Item>
                    );
                }}
            />
        </div>
    );
}

export default ArticleList;